import React, { PureComponent } from 'react';
import { CREATE_SITE_ENTRY, UPDATE_SITE_ENTRY } from '../../graphql/mutation/remote';
import InputAdornment from '@material-ui/core/InputAdornment';
import Form from './Form';
import FormGenerator from './FormGenerator';
import { SITE_ENTRY } from '../../graphql/query/remote';
import { getDate } from '../../utils/date';

// const validate = key => (val, values) => {
//     console.log(val, values);
//     if(Number(values[key].quantity) + val < 0){
//         return "Invalid Input!";
//     }
//     return null;
// }
class CreateSiteEntry extends PureComponent {
    render() {
        const repeater = {
            quantity: {
                type: "number",
                default: 0,
                required: true                
            },
            cost: {
                type: "number",
                default: 0,
                required: true,
                startAdornment: <InputAdornment position="start">â‚¹</InputAdornment>,
                // validate: validate("mistri")
            },
            paid: {
                type: Boolean,
                default: false,
                required: true
            }
        };
        const defaults = {
            mistri: {
                default: repeater                
            },
            labour: {
                default: repeater
            },
            eit: {
                default: repeater
            },
            morang: {
                default: repeater
            },
            baalu: {
                default: repeater
            },
            githi: {
                default: repeater
            },
            cement: {
                default: repeater
            },
            saria: {
                default: repeater
            },
            dust: {
                default: repeater
            },
            other: {
                default: {
                    quantity: {
                        type: "text",
                        default: "N/A",
                        required: true
                    },
                    cost: {
                        type: "number",
                        default: 0,
                        required: true
                    },
                    paid: {
                        type: Boolean,
                        default: false,
                        required: true
                    }
                }
            },
            other2: {
                label: "other 2",
                default: {
                    quantity: {
                        type: "text",
                        default: "N/A",
                        required: true
                    },
                    cost: {
                        type: "number",
                        default: 0,
                        required: true
                    },
                    paid: {
                        type: Boolean,
                        default: false,
                        required: true
                    }
                }
            },
            createdAt: {
                type: "date",
                default: getDate()
            }
        };
        const { siteId, id } = this.props.match.params;
        return (
            <Form
                {...this.props}
                query={SITE_ENTRY}
                createMutation={CREATE_SITE_ENTRY}
                updateMutation={UPDATE_SITE_ENTRY}
                defaults={defaults}>
                {({ update, defaults }) => {
                    return (
                        <FormGenerator
                            {...this.props}
                            defaults={defaults}
                            mutation={update ? UPDATE_SITE_ENTRY : CREATE_SITE_ENTRY}
                            title={!update ? "Create a new entry" : "Update this entry"}
                            submitText={!update ? "Create entry" : "Update entry"}
                            mutateFunc={(fn, data) => { return fn({ variables: { siteId, id, data } }); }}
                        >
                        </FormGenerator>
                    );
                }}
            </Form>
        )
    }
};

export default CreateSiteEntry;



